import Vue from 'vue'
import VueRouter from 'vue-router'
import zh from '../views/zh.vue'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        redirect: '/main1'
    },
    {
        path: '/main1',
        name: 'main2',
        component: () =>
            import ('../views/main1.vue')
    },
    {
        path: '/zh/:id',
        name: 'zh',
        component: zh,
    },
    {
        path: '/gwd',
        name: 'gwd',
        component: () =>
            import ( /* webpackChunkName: "about" */ '../views/gwd.vue')
    },
    {
        path: '/main1',
        name: 'main1',
        component: () =>
            import ('../views/main1.vue')
    }

]

const router = new VueRouter({
    // mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router