            </div>
            <el-divider direction="vertical"></el-divider>
            <div>
              <span name="" @click="login" :class="{ pink: qie }">注册</span>
            </div>
          </div>

          <div class="biaodan">
            <!-- <transition-group leave-active-class="animate__animated  animate__fadeOutRight"> -->
            <el-form
              v-show="qiehuan"
              ref="form"
              :model="form"
              label-width="55px"
              key="deng"
            >
              <el-input
                v-model="form.name"
                clearable
                placeholder="请输入用户名"
              ></el-input>

              <el-input
                v-model="form.password"
                clearable
                show-password
                placeholder="请输入密码"
              ></el-input>

              <el-button type="primary" @click="onSubmit(false)"
                >登入</el-button
              >
            </el-form>
            <el-form
              v-show="qie"
              ref="form"
              :model="form"
              label-width="55px"
              key="zhu"
            >
              <el-input
                v-model="form.name"
                clearable
                placeholder="请输入用户名"
              ></el-input>
              <el-input
                v-model="form.password"
                clearable
                show-password
                placeholder="请输入密码"
              ></el-input>
              <el-input
                v-model="form.phone"
                clearable
                placeholder="请输入手机号"
              ></el-input>
              <el-button type="primary" @click="onSubmit(true)">注册</el-button>
            </el-form>
            <!-- </transition-group> -->
          </div>

          <el-dialog title="提示" :visible.sync="dialogVisible" width="30%">
            <span v-if="qiehuan">登入成功，点击确定直接登入</span>
            <span v-else>注册成功，点击确定直接登入</span>
            <span slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible = false">取 消</el-button>

              <router-link :to="{ name: 'main2' }">
                <el-button type="primary" @click.native="jump"
                  >确 定
                </el-button></router-link
              >
            </span>
