<script>
import axios from "axios";
import bottom1 from '@/components/bottom1.vue'
export default {
  name: "home",

  data() {
    return {
      qiehuan: true,
      form: {
        name: null,
        password: null,
        phone: null,
        id: null,
      },
      dialogVisible: false,
    };
  },
  components:{bottom1},
  methods: {
    login($event) {
      // console.log($event)
      // $event.target.style="color:rgb(241,1,128)"
      var a = $event.target.attributes.name.value;

      this.qiehuan = !!a;
      console.log(this.qiehuan);
    },
    onSubmit(b) {
      this.form.id = this.form.name;
      //   不同的axios请求
      // //  axios.get('http://localhost:8080/posts/1').then(response=>{console.log(response.data);})
      // // axios({
      // //   method:'POST',
      // //   url:'http://localhost:8080/posts',
      // //   data:{
      // //     title:"da",
      // //     author:"da"
      // //   }
      // // }).then(response=>{console.log(response.data)})
      // // }
      // // axios({
      // //   method:'PUT',
      // //   url:'http://localhost:8080/posts/3',
      // //   data:{
      // //     title:'mo',
      // //     author:'活'
      // //   }

      // // }).then(response=>{console.log(response.data)})
      // 以下是注册
      console.log(b);
      if (b) {
        axios
          .post("https://blackskymoonstar.github.io/weipinhui.github.io/api2/users", {
            name: this.form.name,
            password: this.form.password,
            phone: this.form.phone,
            id: this.form.id,
          })
          .then((response) => {
            console.log(response);
            this.dialogVisible = true;
          });
      }
      // 以下是登入
      // console.log(this.form.password)
      else {
        axios.get("https://blackskymoonstar.github.io/weipinhui.github.io/api2/users").then((response) => {
          var a = response.data.filter((x) => {
            return x.name == this.form.name;
          })[0];
          a != undefined
            ? a.password == this.form.password
              ? (this.dialogVisible = true)
              : alert("账号密码有误")
            : alert("账号有误");
        });
      }
    },
    jump() {
      this.dialogVisible = false;
      console.log("跳转");
      // this.$bus.$emit('show1',this.form.name,this.form.password)
      this.$bus.$emit("show1", this.form);
      this.$bus.$emit("dh",'true');
      sessionStorage.setItem("user", JSON.stringify(this.form));
      
    },
  },
  mounted() {
    this.qiehuan = this.$route.params.id;
  },
  computed: {
    qie() {
      return !this.qiehuan;
      // return false
      // if(this.qiehuan){
      //   return false
      // }
      // else{
      //   return true
      // }
    },
  },
};
</script>
