<template>
  <transition
    appear
    leave-active-class="animate__animated  animate__fadeOutRight"
    enter-active-class="animate__animated  animate__fadeInRight"
    mode="in-out"
    :duration="{ enter: 1000, leave: 10000 }"
  >
    <div class="right_d" v-if="show">
      <ul>
        <li @mouseenter="close">
          <i v-if="user"
            ><img
              width="28"
              height="28"
              src="https://h2.appsimg.com/b.appsimg.com/upload/mst/2022/10/13/86/a2a3be4adb5d7c2f18b9212a46ca6b5a_66x66_90.png"
              :alt="user"
          /></i>
          <i v-else>
            <p class="fa fa-user-o fa-2x" />
            <p>账号</p>
          </i>

          <div class="da">
            <div class="shang">
              <div class="user-shadow">
                <img
                  style="
                    border: 1px solid white;
                    border-radius: 50%;
                    margin: 5px;
                  "
                  src="https://shop.vipstatic.com/img/common/sidebar/my_cnt_pic.jpg"
                />
              </div>

              <div style="position: absolute; top: 110px; left: 30%">
                <div v-if="user">
                  {{ user }}<em style="margin-left: 30px">您好</em>
                </div>
                <div v-else>
                  您好！请<router-link
                    @click.native="closewindow"
                    :to="{
                      name: 'zh',
                      params: { id: true },
                    }"
                    active-class="active"
                    >登入</router-link
                  ><el-divider direction="vertical" /><router-link
                    @click.native="closewindow"
                    :to="{
                      name: 'zh',
                      params: { id: false },
                    }"
                    active-class="active"
                    >注册</router-link
                  >
                </div>
              </div>
            </div>
            <div class="zhong">
              <div>
                <i class="fa fa-file-text-o fa-2x" /><span>我的订单</span>
              </div>
              <el-divider direction="vertical"></el-divider>
              <div><i class="fa fa-btc fa-2x" /><span>我的唯品币</span></div>
            </div>
            <div class="xia"><span>会员俱乐部</span></div>
          </div>
          <span
            style="
              height: 8934px;
              width: 200px;
              background: rgb(38, 38, 38);
              z-index: -1;
              position: absolute;
              top: -926px;
            "
          ></span>
        </li>
        <li @click="dengru" ref="wz" name="0" class="tb">
          <i class="fa fa-shopping-bag fa-2x" /><span>购</span><span>物</span
          ><span>袋</span
          ><span
            ><div
              style="
                border: 1px solid white;
                border-radius: 50%;
                background: white;
                color: rgb(223, 20, 127);
                width: 15px;
                margin: 0 9px;
              "
            >
              {{ number }}
            </div></span
          >
        </li>
        <li @click="dengru" name="1">
          <i class="fa fa-smile-o fa-2x" />
          <p class="zhong1">我的优惠券</p>
        </li>
        <li @click="dengru" name="2">
          <i class="fa fa-heart-o fa-2x" />
          <p class="zhong1">品牌收藏</p>
        </li>
        <li @click="posit" name="3">
          <i class="fa fa-star-o fa-2x" />
          <p class="zhong1">商品收藏</p>
        </li>
        <li @click="dengru" name="4">
          <i class="fa fa-history fa-2x" />
          <p class="zhong1">我的足迹</p>
        </li>
      </ul>
      <div ref="dengrumenu" class="dengru-menu">
        <div class="gwcshang">
          <span>{{ dengvalue }}</span
          ><span @click="close" class="fa fa-times fa-2x"></span>
        </div>
        <div class="gwcxia">
          <ul  v-if="dengvalue == '我的浏览记录'">
            <li v-for="(item, index) in good" :key="index">
              <img :src="item.unrl" width="50%" /><span>{{ item.name }}</span
              ><span>1</span><span>{{ item.price }}</span>
            </li>
          </ul>
          <div class="infinite-list-wrapper" style="overflow: auto">
            <ul
              class="list"
              v-infinite-scroll="load"
              infinite-scroll-disabled="disabled"
              infinite-scroll-immediate="true"
              infinite-scroll-distance="0"
              style="max-height: 739px"
              v-if="dengvalue == '购物车'"
            >
              <div class="content1">
                <el-checkbox-group v-model="goods">
                  <li
                    v-for="(item, index) in onloads"
                    :key="index"
                    class="list-item"
                  >
                    <el-checkbox-button :label="index"
                      ><img :src="item.unrl" /><span>{{ item.name }}</span
                      ><span>1</span><span>{{ item.price }}</span>
                      <div class="gn">
                        <!-- <el-button type="danger" round><span>移</span>
                          <span>除</span></el-button> -->
                        <button @click="del" :name="index">
                          <span :name="index">移</span>
                          <span :name="index">除</span>
                        </button>
                      </div>
                    </el-checkbox-button>
                  </li>
                </el-checkbox-group>
              </div>
            </ul>
            <div v-if="dengvalue == '购物车'" style="position: absolute; margin: 20px 40px">
              <el-button-group v-show="this.onloads.length!=0">
                <el-tooltip content="是否全选" placement="top">
                  <el-button
                    v-if="check1"
                    type="primary"
                    icon="el-icon-check"
                    @click="check"
                  ></el-button>
                  <el-button
                    v-else
                    type="primary"
                    icon="el-icon-close"
                    @click="check"
                  ></el-button>
                </el-tooltip>
                <el-tooltip content="删除选择" placement="top">
                  <el-button
                    type="primary"
                    icon="el-icon-delete"
                    @click="alldel"
                  ></el-button>
                </el-tooltip>
                <el-tooltip content="购买选择" placement="top">
                  <el-button
                    type="primary"
                    icon="el-icon-money"
                    @click="buy"
                  ></el-button>
                </el-tooltip>
              </el-button-group>
            </div>
          </div>
          <ul v-if="dengvalue == '我收藏的品牌'">
            <li>12321321</li>
          </ul>
          <ul v-if="dengvalue == '我收藏的商品'">
            <li>12321321</li>
          </ul>
          <ul v-if="dengvalue == '我的优惠券'">
            <li>12321321</li>
          </ul>
        </div>
      </div>
      <ol class="bottom">
        <li>
          <i class="fa fa-pencil fa-2x" /><span class="xiao">会员反馈</span>
        </li>
        <li @click="top">
          <i class="fa fa-chevron-up fa-2x" /><span class="xiao" 
            >返回顶部</span
          >
        </li>
      </ol>
    </div>
  </transition>
</template>

<script>
import axios from "axios";

export default {
  name: "right-d",
  data() {
    return {
      // isCollapse: true,
      form: "",
      dengruvalue2: "",
      show: true,
      user: "",
      password: "",
      dengvalue: "",
      duo: [
        "购物车",
        "我的优惠券",
        "我收藏的品牌",
        "我收藏的商品",
        "我的浏览记录",
      ],
      qie: false,
      good: [],
      goods: [],
      count: 0,
      loading: false,
      onloads: [],
      timer: "",
      number: 0,
      check1: true,
      zj:0
    };
  },
  methods: {
    posit($event) {
      console.log($event.clientX, $event.clientY);
    },
    // gotop(el){ el.scrollIntoView();},
    closewindow() {
      this.show = false;
      // console.log(this.show)
    },
    dengru($event) {
      if (this.user && this.qie == false) {
        console.log("弹出" + this.user, this.qie);
        this.dengvalue =
          this.duo[$event.target.parentElement.attributes.name.value];
        this.dengruvalue2 = this.dengvalue;
        this.$refs.dengrumenu.style.right = 36 + "px";
        this.qie = true;
        this.load();
      } else if (
        this.qie &&
        this.dengvalue ==
          this.duo[$event.target.parentElement.attributes.name.value]
      ) {
        console.log("收起" + this.dengruvalue2, this.dengvalue);
        this.qie = false;
        this.$refs.dengrumenu.style.right = -235 + "px";
      }
      this.dengvalue =
        this.duo[$event.target.parentElement.attributes.name.value];
      // else if(this.dengvalue==){
      //   console.log(12222)
      //   this.dengvalue =this.duo[$event.target.parentElement.attributes.name.value];
      // }
      // else{
      //   this.$refs.dengrumenu.style.right = -235 + "px";
      //   console.log(1111)
      // }

      // console.log($event.target.parentElement.attributes.name.value)
    },
    close() {
      this.qie = false;
      this.$refs.dengrumenu.style.right = -235 + "px";
    },
    top() /* 点击按钮触发 */ {
      scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
    del($event) {
      console.log(typeof $event.target.attributes[1]);
      try {
        console.log(typeof number($event.target.attributes.name.value));
      } catch (error) {
        console.log("不能这么转");
      }

      console.log("拿到商品下标" + $event.target.attributes.name.value);

      this.onloads = this.good = this.good.filter(function (item, index) {
        console.log(index != $event.target.attributes.name.value);
        return index != $event.target.attributes.name.value;
        // return false
      });

      // console.log(this.good)
    },
    // load() {
    //   this.loading = true;
    //   console.log("加载数据" + this.good, this.count,this.good.length >= this.count);
    //   this.timer = setTimeout(() => {
    //     if (this.good && this.good.length >= this.count) {
    //       this.count += 2;
    //       console.log("截取0到" + this.count + "条数据");
    //       console.log("所有数据长度：" + this.good.length);
    //       // console.log(this.good.slice(0,this.count))
    //       this.onloads = this.good.slice(0, this.count);
    //       console.log(this.onloads);
    //       this.loading = false;
    //     }
    //   }, 250);
    //   this.loading = false;
    // },
    check() {
      this.check1 = !this.check1;
      if (!this.check1) {
        for (var a = 0; a < this.onloads.length; a++) {
          this.goods.push(a);
        }
      } else {
        this.goods = [];
      }
    },
    alldel() {
      // console.log(typeof this.goods[0]);
        console.log(...this.goods)
        this.onloads=this.onloads.filter((item, index) => {
         
          // console.log(index,item,this.goods)
          
          // console.log
          // console.log(this.goods.indexOf(index))
          // console.log(this.goods.indexOf(index)<0)
          return this.goods.indexOf(index)<0
          
        })
        console.log(this.onloads)
        this.goods=[]
        this.number=this.onloads.length
        this.check1=!this.check1
        
      
    },
   
    buy() {
      
      let bd=this
      this.onloads.filter((item, index) => {return this.goods.indexOf(index)>=0}).forEach(function(item1, i){
        console.log(typeof item1,item1)
        //  let ok=item1
         item1=item1.price.split("折");
         console.log(item1,parseFloat(item1[0]))
         item1=parseFloat(item1[0])*parseFloat(item1[1])
         console.log(item1)
         console.log(bd.zj)
         bd.zj+=item1
         console.log(bd.zj)
        })
        this.$confirm("共付款"+this.zj)
          .then(_ => {
            this.alldel()
          })
          .catch(_ => {});
      
    },
    resizeFun(){
      setTimeout(() => {
        console.log("执行了")
        this.$bus.$emit("wzok", this.$refs.wz.offsetWidth);
      }, 100);
      
    }
  },
  beforeDestroy() {
  window.removeEventListener('resize',this.resizeFun)
},
  
  mounted() {
    //
    this.$nextTick(() => {
    
    this.resizeFun()
    console.log(this.$router,"执行了")
    // this.resizeFun窗口变化执行的方法
    window.addEventListener('resize',this.resizeFun )
  })
    // setTimeout(() => {
    //   this.$bus.$emit("wzok", this.$refs.wz.offsetWidth);
    // }, 100);





    this.$bus.$on("show1", (form) => {
      console.log("登入完毕");
      this.show = true;
      this.user = form.name;
      this.password = form.password;
      this.form = form;
      console.log(form.name, form);
      console.log(this.form);
      axios
        .get("https://blackskymoonstar.github.io/weipinhui.github.io/api2/users/" + form.id)
        .then((response) => {
          console.log(response.data);
          this.good = this.onloads = response.data.shopping;
          this.number = response.data.shopping.length;
          
        });
    });
    this.$bus.$on("gw", (gw) => {
      console.log("原商品"+this.onloads)
      console.log("收到商品");
      console.log(gw);
      this.good.push(gw)
      this.onloads.push(gw)
      // this.good = gw;
      this.number = this.onloads.length;
      console.log(this.good);
    });
    if (sessionStorage.getItem("user")) {
      console.log("有缓存");
      this.form = JSON.parse(sessionStorage.getItem("user"));
      this.user = this.form.name;
      this.password = this.form.password;
      axios
        .get("https://blackskymoonstar.github.io/weipinhui.github.io/api2/users/" + form.id)
        .then((response) => {
          console.log(response.data);
          this.good = this.onloads = response.data.shopping;
          this.number = response.data.shopping.length;
        });
    }
  },

  computed: {
    
    // onloads:{
    //   get(){
    //     console.log("初始化数据购物车")
    //   return this.good.slice(0,8)
    //   },
    //   set(val){
    //     console.log("添加数据")
    //     this.onloads.push(val)
    //   }

    // },
    noMore() {
      return this.onloads.length >= this.good.length;
    },
    disabled() {
      console.log(this.loading || this.noMore, this.loading, this.noMore);
      return this.loading || this.noMore;
    },
  },
  beforeDestroy() {
    console.log("beforeDestroy");
    clearInterval(this.timer);
  },
  watch: {
    onloads: {
      handler(item, item2) {
        try {
          console.log(this.form.id, item);

          axios
            .put("https://blackskymoonstar.github.io/weipinhui.github.io/api2/users/" + this.form.id, {
              name: this.user,
              password: this.password,
              phone: this.form.phone,
              id: this.form.id,
              shopping: item,
            })
            .then((response) => {
              console.log(response);
            });
        } catch (error) {
          console.log("没有登入");
        }
      },
      deep: true,
    },
  },
};
</script>

<style lang="less" scoped>
// .content1{
//   li{
//     background:
//   }
// }
.right_d {
  z-index: 10;
  position: fixed;
  // z-index: 50;
  min-height: 560px;
  width: 36px;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  // z-index:75;
  background: rgb(38, 38, 38);
  display: flex;
  align-items: center;

  // justify-content: center;
  .dengru-menu {
    right: -235px;
    background: white;
    color: black;
    position: absolute;
    width: 270px;
    height: 100%;
    top: 0px;
    // right: -235px;
    // background: red;
    z-index: -5;
    transition: all 0.5s linear;
    box-sizing: border-box;
    padding: 10px;
    .gwcshang {
      display: flex;
      justify-content: space-between;
      padding-bottom: 6px;
      border-bottom: 1px solid;
    }
    .gwcxia {
      li {
        border: 1px solid rgb(207, 207, 207);
        border-radius: 3px;
        margin: 3px;
        padding: 7px;
        height: 230px;
      }
      label {
        border: 1px solid rgb(207, 207, 207);
        border-radius: 3px;
        margin: 3px;
        padding: 7px;
        height: 206px;
        // .el-checkbox-button__inner{
        //   border-width: initial;
        //   border-style: none;
        //   border-image: initial;
        //   border-radius: 0px!important;
        // }
        .gn {
          background: red;
          height: 112px;
          width: 33px;
          position: absolute;
          right: -67px;
          top: 27px;
          justify-content: space-between;

          :first-child,
          :last-child {
            width: 31px;
            background: red;

            display: flex;
            flex-direction: column;
            justify-content: space-between;
            line-height: 35px;

            color: white;
            // background: white;
            border-color: white;
            // border: 1px solid;
            height: 72px;
            cursor: pointer;
          }
        }
        img {
          width: 81px;
        }
      }
    }
  }

  ul {
    // position: relative;
    // z-index: 60;
    width: 100%;
    height: 85%;
    flex-direction: column;
    font-size: 10px;
    color: white;

    li {
      background: rgb(38, 38, 38);
      // z-index: 70;
      margin: 1px 0 1px 0;

      position: relative;
      i {
        padding: 11px 0px 11px 0px;
        position: relative;
        z-index: 50;
      }
      .da {
        background: white;
        transition: all 0.3s linear;
        position: absolute;
        z-index: -1;
        top: 0px;
        height: 318px;
        width: 300px;
        border: 1px solid #dddddd;
        border-right: none;
        left: 0px;
        border-radius: 3px 0 0 3px;
        .shang {
          font-size: 14px;
          position: relative;
          text-align: center;
          justify-content: center;
          align-items: center;
          width: 100%;
          height: 160px;
          color: black;
          a {
            color: black;
          }
          .user-shadow {
            width: 78px;
            height: 78px;
            border: 1px solid transparent;
            border-radius: 50%;
            box-shadow: 2px 3px 5px rgb(235, 235, 235);
            position: absolute;
            top: 15px;
