          }
        }
        .xia {
          transition: all 0.3s linear;
          font-size: 14px;
          height: 50px;
          align-items: center;
        }
      }
      .zhong1 {
        background: white;
        z-index: -1;
        position: absolute;
        top: 0px;
        left: 0px;
        width: 114px !important;
        border: 1px solid rgb(221, 221, 221);
        border-right: none;
        height: 41px;
        border-radius: 3px 0 0 3px;
