      }
      &:hover {
        background: rgb(223, 20, 127);
      }
      &:hover .zhong1 {
        transition: all 0.3s linear;
        left: -114px;
        // font-size: 14px;

        color: black !important;
      }
      &:hover .da {
        left: -301px;
        transition: all 0.3s linear;
        font-size: 14px;
        color: black !important;
      }
      a {
        color: white;
      }
      i,
      span {
        display: block;
        text-align: center;
      }
      .tb > i,
      span {
        padding: 10px 0px 10px 0px;
      }
    }
    .tb {
      background: rgb(223, 20, 127);
    }
  }
}
// 上面是上面的li,下面是下面剩余的列
.bottom {
  // z-index: 99;
  font-size: 10px;
  align-items: flex-end;
  display: block;
  position: absolute;
  bottom: 15px;
  width: 100%;
  li {
    margin: 1px 0 1px 0;
    padding: 5px 0px 5px 0px;
    position: relative;
    background: rgb(38, 38, 38);
    .xiao {
      background: white;
      text-align: center;
      box-sizing: border-box;
      align-content: center;
      line-height: 29.67px;
      z-index: -1;
